<template>
  <div class="content">
    <div class="md-layout">
      <div class="md-layout-item md-medium-size-100 md-size-100">
        <md-card>
          <md-card-header data-background-color="blue">
            <h4 class="title">
              <i class="pi pi-user" style="fontSize: 1rem"></i>
              Login
            </h4>
            <p class="category">Complete para ingresar</p>
          </md-card-header>
          <md-card-content>
            <div class="md-layout">
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Correo</label>
                  <md-input v-model="correo" type="email"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-small-size-100 md-size-100">
                <md-field>
                  <label>Clave</label>
                  <md-input v-model="clave" type="password"></md-input>
                </md-field>
              </div>
              <div class="md-layout-item md-size-100 text-right">
                <md-button
                  class="md-raised md-success"
                  @click.native.prevent="Ingresar"
                >
                  Ingresar</md-button
                >
              </div>
            </div>
          </md-card-content>
        </md-card>
      </div>
    </div>
  </div>
</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import Crypto from "crypto-js";

export default {
  data() {
    return {
      estatus: false,
      correo: null,
      clave: null,
      validar: false,
      mens: null
    };
  },
  created() {
    this.$store.commit('Logout');
  },
  methods: {
    Ingresar() {
      if (this.correo == null || this.clave == null) {
        this.$notify(Mensaje(3, "Debe ingresar los datos solicitados."));
      } else if (
        !/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
          this.correo
        )
      ) {
        this.$notify(Mensaje(3, "El correo no es válido"));
      } else {
        this.validar = true;
      }
      if (this.validar) {
        const cifrado = Crypto.HmacSHA1(this.clave, "btcoin").toString();
        const Consulta = new API(this.correo,cifrado);
		    Consulta.Ini('login').then(response => {
     		  if (response.authenticated) {
					  this.$store.commit('Seguridad',{
							correo: this.correo, 
							clave: cifrado, 
							usuario: response.user.nombre + ' ' + response.user.apellido,
							rol: response.user.rol
            });
 						this.$store.commit('Menu',{
						  rol: response.rol
            });
            this.$router.push('dashboard');
          } else {
            this.$notify(Mensaje(3, 'Usuario o Clave incorrectas!'));
          }


        }); 



      }
    }
  }
};
</script>
