              ></div>
              <div
                class="md-layout-item md-size-70 text-right"
                v-else
              ></div>
              <div
                class="md-layout-item md-size-15 md-small-size-100 text-right"
                v-if="disabled2"
              >
                <md-list-item @click="Editar(2)">
                  <div class="stats">
                    <md-icon>edit</md-icon>Editar
                  </div>
                </md-list-item>
              </div>
              <div
                class="md-layout-item md-size-15 md-small-size-100 text-right"
