</template>

<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT("testnet");

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      error: null,
      mercado: 0,
      porcentaje: 0,
      comision: 0,
      retiro: 0,
      wallet: null,
      balance: 0,
      WIF: null,
      address: {
        proveedor: "-",
        comision: "-",
        servicio: "-"
      },
      Hex: null,
      disabled0: true,
      disabled1: true,
      disabled2: true,
      hash: null,
      valor: null,
      transactions: 0,
      ver: 0,
      portafolio: []
    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      this.error = null;
      this.wallet = null;
      this.balance = 0;
      this.porcentaje = 0;
      this.comision = 0;
      this.retiro = 0;
      this.WIF = null;
      this.address = {};
      this.Hex = null;
      this.disabled0 = true;
      this.disabled1 = true;
      this.disabled2 = true;
      this.hash = null;
      this.valor = null;
      this.portafolio = [];
      this.transactions = 0;
      this.ver = 0;
      this.utxos_disp = 0;
      const Consulta = new API();
      Consulta.Ini('configuracion').then(response => {
        this.porcentaje = response.configuracion.price_spread;
        this.comision = response.configuracion.comision;
        this.retiro = response.configuracion.retiro_antic;
        const decWIF = secure.decrypt(
          response.configuracion.proveedor_wif,
          "btcoin"
        );
        this.WIF = decWIF;
        this.address.proveedor = response.configuracion.proveedor_address;
        this.address.comision = response.configuracion.comision_address;
        this.address.servicio = response.configuracion.servicio_address;
        Bitcoin.privateKeyToAccount(decWIF);
        this.wallet = Bitcoin.defaultAccount.address;
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat(result.bpi.USD.rate.replace(",", "")).toFixed(
            2
          );
          this.mercado = btc;
        });
        Bitcoin.getBalance().then(result => {
          this.transactions =
            result.chain_stats["tx_count"] + result.mempool_stats["tx_count"];
          const received = result.chain_stats["funded_txo_sum"];
          const spent = result.chain_stats["spent_txo_sum"];
          this.balance = ((received - spent) / 100000000).toFixed(8);
        });
        Consulta.Ini('portafolio').then(response => {
          this.portafolio = response.portafolio;
         
        });
      });
    },
    Editar(caso) {
      if (caso == 0) {
        this.disabled0 = false;
      }
      if (caso == 1) {
        this.disabled1 = false;
      }
      if (caso == 2) {
        this.disabled2 = false;
      }
    },
    Guardar(caso) {
      if (!Bitcoin.isValidAddress(this.address.servicio)) {
        this.$notify(Mensaje(3, "Address de Servicio Inválida."));
      } else if (!Bitcoin.isValidAddress(this.address.comision)) {
        this.$notify(Mensaje(3, "Address de Servicio Inválida."));
      } else if (!Bitcoin.isValidAddress(this.address.proveedor)) {
        this.$notify(Mensaje(3, "Address de Proveedor Inválida."));
      } else if (this.address.proveedor != this.wallet) {
        this.$notify(Mensaje(3, "Private Key de Proveedor Inválida"));
      } else {
        if (caso == 2) {
          for (let i = 0; i < this.portafolio.length; i++) {
            const UPD = new API();
            UPD.Procesar("updateportafolio_porc",{
              id: this.portafolio[i].id,
              porcentaje_inver: this.portafolio[i].porcentaje_inver
            }).then(response => {
              if (response.error) {
                this.$notify(Mensaje(3, response.message));
              } else {
                this.$notify(Mensaje(0, response.message));
                this.portafolio = [];
                this.Mostrar();
              }
            });
          }
        } else {
          const UPD = new API();
          UPD.Procesar("updateconfiguracion",{
            price_spread: this.porcentaje,
            comision: this.comision,
            retiro_antic: this.retiro,
            proveedor_address: this.address.proveedor,
            proveedor_wif: secure.encrypt(this.WIF, "btcoin"),
            servicio_address: this.address.servicio,
            comision_address: this.address.comision
          }).then(response => {
            if (response.error) {
              this.$notify(Mensaje(3, response.message));
              this.Mostrar();
            } else {
              this.$notify(Mensaje(0, response.message));
            }
          });
        }
        if (caso == 0) {
          this.disabled0 = true;
        }
        if (caso == 1) {
          this.disabled1 = true;
        }
        if (caso == 2) {
          this.disabled2 = true;
        }
      }
    },
    Cerrar(caso) {
      if (caso == 0) {
        this.disabled0 = true;
      }
      if (caso == 1) {
        this.disabled1 = true;
      }
      if (caso == 2) {
        this.disabled2 = true;
      }
    }
  }
};
</script>
