          <p class="category">
            Cantidad de Transacciones: {{ movimientos.length }}
          </p>
        </md-card-header>
        <md-card-content>
          <md-table v-model="movimientos">
            <md-table-row slot="md-table-row" slot-scope="{ item }">
              <md-table-cell md-label="TxID">{{ item.hash }}</md-table-cell>
              <md-table-cell md-label="Valor BTC">{{
                item.satoshis
              }}</md-table-cell>
              <md-table-cell md-label="Fecha">{{ item.fecha }}</md-table-cell>
              <md-table-cell md-label="Estado">{{
