<script>
import API from "../service/ApiConsulta";
import Mensaje from "../globalNotificaciones";
import axios from "axios";
import CoinKey from "coinkey";
import ci from "coininfo";
import * as secure from "securejs";
import BT from "../Bitcoin";
const Bitcoin = new BT("testnet");
import QR from "qr-encode";

import { StatsCard } from "@/components";

export default {
  components: {
    StatsCard
  },

  data() {
    return {
      error: null,
      wallet: null,
      qr: null,
      saldo: 0,
      mercado: 0,
      balance: 0,
      WIF: null,
      disabled: true,
      valor: null,
      ver: 0,
      transactions: 0,
      utxos: [],
      utxos_disp: 0,
      movimientos: []
    };
  },
  created() {
    this.Mostrar();
  },
  computed: {},
  methods: {
    Mostrar() {
      this.error = null;
      this.wallet = null;
      this.balance = 0;
      this.WIF = null;
      this.disabled = true;
      this.hash = null;
      this.valor = null;
      this.saldo = 0;
      this.transactions = [];
      this.utxos = [];
      this.movimientos = [];
      this.transactions = 0;
      this.ver = 0;
      this.utxos_disp = 0;
      const Consulta = new API();
      Consulta.Ini('configuracion').then(response => {
        this.wallet = response.configuracion.proveedor_address;
        const decWIF = secure.decrypt(
          response.configuracion.proveedor_wif,
          "btcoin"
        );
        this.WIF = decWIF;
        this.saldo = parseFloat(response.saldo).toFixed(2);
        const porcentaje = parseFloat(response.configuracion.price_spread);
        var dataURI = QR(this.wallet, { type: 6, size: 5, level: "Q" });
        var img = new Image();
        img.src = dataURI;
        this.qr = img.src;
        Bitcoin.currentprice().then(result => {
          const btc = parseFloat(result.bpi.USD.rate.replace(",", ""));
          this.mercado = ((btc * (100 + porcentaje)) / 100).toFixed(2);
        });
        Bitcoin.getBalance(this.wallet).then(result => {
          this.transactions =
            result.chain_stats["tx_count"] + result.mempool_stats["tx_count"];
          const received = result.chain_stats["funded_txo_sum"];
          const spent = result.chain_stats["spent_txo_sum"];
          this.balance = ((received - spent) / 100000000).toFixed(8);
        });
        Bitcoin.getUtxo(this.wallet).then(result => {
          this.utxos = result;
          let disp = 0;
          for (let i = 0; i < result.length; i++) {
            if (result[i]["confirmed"]) {
              disp += result[i]["satoshis"];
            }
          }
          this.utxos_disp = disp;
        });
        Bitcoin.getTxs(this.wallet).then(result => {
          //this.error=result[0].status.block_time;
          //var time = new Date().getTime();
          result.forEach(element => {
            let value = 0;
            for (let i = 0; i < element["vout"].length; i++) {
              value = value + element["vout"][i]["value"];
            }
            let estado = "Procesando";
            if (element["status"]["confirmed"]) {
              estado = "Procesada";
            }
            var date = new Date(1000 * element["status"]["block_time"]);
            this.movimientos.push({
              hash: element["txid"],
              satoshis: (value / 100000000).toFixed(8),
              confirmed: estado,
              fecha: date.toString()
            });
          });
        });
      });
    },
    Ver(caso) {
      if (this.ver == caso) {
        this.ver = 0;
      } else {
        this.ver = caso;
      }
    }
  }
};
</script>
